<script>
import { mapGetters } from "vuex";
export default {
  data() {
    return {
      hover: false,
      m: "Customized for Fyish"
    };
  },
  computed: {
    ...mapGetters(["GETmousePosition"])
  },
  props: {
    excel: {
      type: String,
      default() {
        return {};
      }
    },
    color: {
      type: String,
      default() {
        return {};
      }
    },
    routerActive: {
      type: String,
      default() {
        return {};
      }
    },
    hoverActive: {
      type: String,
      default() {
        return {};
      }
    },
    message: {
      type: String,
      default() {
        return {};
      }
    },
    width: {
      type: String,
      default() {
        return {};
      }
    },
    height: {
      type: String,
      default() {
        return {};
      }
    }
  },
  methods: {
    controlHover(a) {
      var c = this.$refs.maker.getBoundingClientRect();
      if (c.top < a.y && a.y < c.bottom && c.left < a.x && a.x < c.right) {
        this.hover = true;
      } else {
        this.hover = false;
      }
    },
    setPosition() {
      var c = this.$refs.maker.getBoundingClientRect();
      this.$store.commit("HoverOne", {
        a: c,
        access: this.hover,
        message: this.message
      });
    }
  },
  watch: {
    hover() {
      this.setPosition();
    },
    GETmousePosition() {
      if (this.hoverActive === "true") this.controlHover(this.GETmousePosition);
    }
  }
};
</script>
