                {{ url }}
              </div>
              <div
                class="fy-font-13 fy-font_w600 fy-text-1 fy-ats"
                :class="{ 'fy-wc1': load.t === 1, 'fy-wc2': load.t === 2 }"
              >
                {{ load.T }}
              </div>
              <div class="fy-font-11 fy-font_w500 fy-text-1 ">
                <div
                  class="fy-ats"
                  :class="{ 'fy-wc3': load.t === 1, 'fy-wc2': load.t === 2 }"
                >
                  {{ load.d }}
                </div>
              </div>
            </div>
          </div>
