<script>
import { mapGetters } from "vuex";
//import {browse} from '@/device/browser.js'
export default {
  data() {
    return {
      icon: false,

      sessionGet: false,
      url: "",
      load: ""
    };
  },
  props: {
    data: null,
    img: null
  },
  created() {},
  computed: {
    ...mapGetters(["GETapilink", "GETcdnlink", "GETheight"]),

    height() {
      if (this.load.t === 2) {
        var h = "";
      } else {
        h = "104px";
      }
      return h;
    }
  },
  mounted() {
    this.setLoad();
  },
  methods: {
    browse(a) {
      window.open(a, "_blank");
    },
    setLoad() {
      var y = `${this.data}`;

      if (y !== "undefined" && y !== undefined && y !== null && y !== "") {
        this.load = JSON.parse(y);

        var t = this.load.u;
        if (t) {
          var i = t.split("//")[1];
          if (i) {
            var o = i.split("/")[0];
            this.url = o.toUpperCase();
          }
        }
      }
    }
  },
  watch: {}
};
</script>
